.fireworkContainer {
    position: absolute;
    top: 0;
    left: 0;
    height: 100vh;
    width: 100vw;
    z-index: -1;
    /*background: radial-gradient(ellipse at bottom, #1b2735 0%, #090a0f 100%);*/

    overflow: hidden;
    display: flex;
    font-family: sans-serif;
    justify-content: center;
    align-items: center;
    perspective: 600px;
}

div {
    transform-style: preserve-3d;
}

.camera.-y {
    -webkit-animation: rotate 30s linear infinite;
    animation: rotate 30s linear infinite;
}

.fireworks {
    transform: translateY(-100px);
}

.spark {
    position: absolute;
    width: 800px;
    height: 40vh;
    transform-origin: 0 0;
}

.fire {
    position: absolute;
    left: -3px;
    width: 7px;
    height: 7px;
    background: #fa2828;
}
.fire::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    background: #e6c828;
    transform: translateZ(0.1px);
}

.line:nth-child(1) {
    transform: rotateY(171deg);
}
.line:nth-child(1) .spark {
    width: 3556px;
    height: 3410px;
    -webkit-animation: spark1 5s cubic-bezier(0.39, 0.575, 0.565, 1) infinite, opacity 5s ease-out infinite;
    animation: spark1 5s cubic-bezier(0.39, 0.575, 0.565, 1) infinite, opacity 5s ease-out infinite;
}
.line:nth-child(1) .fire {
    -webkit-animation: fire 1479ms -386ms linear infinite;
    animation: fire 1479ms -386ms linear infinite;
}
@-webkit-keyframes spark1 {
    0% {
        transform: translateY(1512px);
    }
    50% {
        transform: translateY(0);
    }
    100% {
        transform: rotateZ(91deg) translateX(15vw);
    }
